/**
 * Cliente HTTP para comunicarse con el backend RPA
 * Este archivo maneja la comunicacin entre el add-in de Outlook y el servidor RPA
 */

// URLs del servidor RPA (se configuran dinmicamente segn el entorno)
// En produccin, estas URLs se inyectan durante el build de webpack
const RPA_CREATE_URL = typeof RPA_API_URL !== 'undefined' 
  ? RPA_API_URL + '/api/rpa/create-reservation'
  : 'http://localhost:3001/api/rpa/create-reservation';

const RPA_EDIT_URL = typeof RPA_API_URL !== 'undefined' 
  ? RPA_API_URL + '/api/rpa/edit-reservation'
  : 'http://localhost:3001/api/rpa/edit-reservation';

/**
 * Transforma los datos del formulario al formato esperado por el RPA
 * @param {Array} pasajeros - Array de objetos con datos de pasajeros
 * @param {Object} datosReserva - Datos de la reserva del formulario
 * @param {boolean} isEdit - Si es true, incluye originData
 * @param {Object} originData - Data original de la extraccin (solo para edicin)
 * @returns {Object} Datos formateados para el RPA
 */
function transformarDatosParaRPA(pasajeros, datosReserva = {}, isEdit = false, originData = null) {
  const datosRPA = {
    passengers: pasajeros.map(p => ({
      lastName: p.apellido || '',
      firstName: p.nombre || '',
      paxType: p.tipoPasajero === 'adulto' ? 'ADU' : p.tipoPasajero === 'menor' ? 'CHD' : 'INF',
      birthDate: formatearFecha(p.fechaNacimiento), // Convertir a MM/DD/AAAA
      nationality: p.nacionalidad?.toUpperCase() || 'ARGENTINA',
      sex: (() => {
        // Normalizar el valor de sexo: puede venir como "M", "F", "[M]", "[F]", "masculino", "femenino", etc.
        let sexoValue = p.sexo || '';
        
        // Si est vaco, retornar vaco
        if (!sexoValue) return '';
        
        // Convertir a maysculas para normalizar
        sexoValue = sexoValue.toUpperCase().trim();
        
        // Si ya tiene corchetes, extraer el cdigo
        if (sexoValue.startsWith('[') && sexoValue.endsWith(']')) {
          sexoValue = sexoValue.slice(1, -1);
        }
        
        // Mapear valores comunes a cdigos
        if (sexoValue === 'M' || sexoValue === 'MASCULINO' || sexoValue === 'MALE') {
          return '[M]';
        } else if (sexoValue === 'F' || sexoValue === 'FEMENINO' || sexoValue === 'FEMALE') {
          return '[F]';
        } else if (sexoValue === 'O' || sexoValue === 'OTRO' || sexoValue === 'OTHER') {
          return '[O]';
        }
        
        // Si no coincide con ninguno, agregar corchetes si no los tiene y retornar
        if (sexoValue.length === 1) {
          return `[${sexoValue}]`;
        }
        
        // Si tiene ms de un carcter y no tiene corchetes, intentar extraer el cdigo
        return sexoValue;
      })(),
      documentNumber: p.dni || '',
      documentType: p.tipoDoc?.toUpperCase() || 'DNI',
      cuilCuit: p.cuil || '',
      phoneNumber: p.telefono || '',
      direccion: p.direccion || ''
    })),
    conversationId: datosReserva.conversationId || null,
    emailId: datosReserva.itemId || null, // ID del email para Graph API
    reservationType: datosReserva.tipoReserva || 'AGENCIAS [COAG]',
    status: datosReserva.estadoReserva || 'PENDIENTE DE CONFIRMACION [PC]',
    client: datosReserva.cliente || 'DESPEGAR - TEST - 1',
    travelDate: formatearFecha(datosReserva.fechaViaje) || '12/01/2026',
    seller: datosReserva.vendedor || 'TEST TEST',
    codigo: datosReserva.codigo || '',
    reservationDate: formatearFecha(datosReserva.reservationDate) || '',
    tourEndDate: formatearFecha(datosReserva.tourEndDate) || '',
    dueDate: formatearFecha(datosReserva.dueDate) || '',
    contact: datosReserva.contact || '',
    contactEmail: datosReserva.contactEmail || '',
    contactPhone: datosReserva.contactPhone || '',
    currency: datosReserva.currency || 'ARS',
    exchangeRate: datosReserva.exchangeRate || 0,
    commission: datosReserva.commission || 0,
    netAmount: datosReserva.netAmount || 0,
    grossAmount: datosReserva.grossAmount || 0,
    tripName: datosReserva.tripName || '',
    productCode: datosReserva.productCode || '',
    adults: datosReserva.adults || 0,
    children: datosReserva.children || 0,
    infants: datosReserva.infants || 0,
    provider: datosReserva.provider || '',
    reservationCode: datosReserva.reservationCode || '',
    hotel: datosReserva.hotel && typeof datosReserva.hotel === 'object' 
      ? datosReserva.hotel 
      : (datosReserva.hotel ? { nombre_hotel: datosReserva.hotel, tipo_habitacion: '', Ciudad: '', Categoria: null } : null),
    checkIn: formatearFecha(datosReserva.checkIn) || '',
    checkOut: formatearFecha(datosReserva.checkOut) || '',
    estadoDeuda: datosReserva.estadoDeuda || '',
    services: datosReserva.services || [],
    flights: datosReserva.flights || []
  };
  
  // Si es edicin, incluir la data original
  if (isEdit && originData) {
    datosRPA.originData = originData;
  }
  
  return datosRPA;
}

/**
 * Formatea una fecha de YYYY-MM-DD a MM/DD/YYYY
 * @param {string} fecha - Fecha en formato YYYY-MM-DD
 * @returns {string} Fecha en formato MM/DD/YYYY
 */
function formatearFecha(fecha) {
  if (!fecha) return '';
  
  try {
    const [year, month, day] = fecha.split('-');
    return `${month}/${day}/${year}`;
  } catch (error) {
    return '';
  }
}

/**
 * Enva los datos al servicio RPA para crear o editar la reserva
 * @param {Array} pasajeros - Array de objetos con datos de pasajeros del formulario
 * @param {Object} datosReserva - Datos de la reserva del formulario
 * @param {boolean} isEdit - Si es true, edita la reserva; si es false, crea una nueva
 * @param {Object} originData - Data original de la extraccin (solo para edicin)
 * @returns {Promise<Object>} Resultado de la operacin
 */
export async function crearReservaEnITraffic(pasajeros, datosReserva = {}, isEdit = false, originData = null) {
  try {
    // Transformar datos al formato del RPA
    const datosRPA = transformarDatosParaRPA(pasajeros, datosReserva, isEdit, originData);
    
    // Seleccionar la URL segn si es crear o editar
    const serviceUrl = isEdit ? RPA_EDIT_URL : RPA_CREATE_URL;
    
    // Enviar peticin al servidor RPA
    const response = await fetch(serviceUrl, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(datosRPA)
    });
    
    const resultado = await response.json();
    
    // Si la respuesta no es exitosa o success es false, lanzar error con el mensaje del backend
    if (!response.ok || resultado.success === false) {
      const errorMessage = resultado.error || resultado.message || `Error del servidor: ${response.status} ${response.statusText}`;
      throw new Error(errorMessage);
    }
    
    return {
      success: resultado.success !== false,
      data: resultado.data || resultado,
      reservationCode: resultado.reservationCode || null,
      message: resultado.message || (isEdit ? 'Reserva editada exitosamente en iTraffic' : 'Reserva creada exitosamente en iTraffic')
    };
    
  } catch (error) {
    // Si el servidor no est disponible, mostrar un mensaje ms amigable
    if (error.message.includes('fetch')) {
      throw new Error('No se pudo conectar con el servicio RPA. Asegrate de que el servidor est corriendo.');
    }
    
    throw error;
  }
}

/**
 * Verifica si el servicio RPA est disponible
 * @returns {Promise<boolean>} true si el servicio est disponible
 */
export async function verificarServicioRPA() {
  try {
    const healthUrl = typeof RPA_API_URL !== 'undefined'
      ? RPA_API_URL + '/api/rpa/health'
      : 'http://localhost:3001/api/rpa/health';
      
    const response = await fetch(healthUrl, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      }
    });
    
    return response.ok;
  } catch (error) {
    console.error('Error verificando servicio RPA:', error);
    return false;
  }
}
